/* navigation */
#menu_toggle { display: none; float: right; position: relative; overflow: hidden; margin-top: 10px; margin-left: 25px; margin-bottom: 10px; width: 24px; height: 24px; font-size: 0; text-indent: -9999px; appearance: none; background-color: transparent; cursor: pointer; transition: background 0.3s; transition: margin 0.2s; }
