#menu_toggle span { display: block; position: absolute; top: 50%; left: 0; right: 0; height: 2px; background: white; transition: background 0s 0.3s; }
#menu_toggle span:before, #menu_toggle span:after { position: absolute; display: block; left: 0; width: 100%; height: 2px; background-color: #fff; content: ""; transition-duration: 0.3s, 0.3s; transition-delay: 0.3s, 0s; }
#menu_toggle span:before { top: -7px; transition-property: top, transform; }
#menu_toggle span:after { bottom: -7px; transition-property: bottom, transform; }
#menu_toggle.is-active { background-color: transparent; }
#menu_toggle.is-active span { background: none; }
#menu_toggle.is-active span:before { top: 0; transform: rotate(45deg); }
#menu_toggle.is-active span:after { bottom: 0; transform: rotate(-45deg); }
