/* Base for label styling */
[type="radio"]:not(:checked), [type="radio"]:checked { position: absolute; left: -9999px; }

[type="radio"]:not(:checked) + label, [type="radio"]:checked + label { position: relative; padding-left: 32px; font-size: 18px; line-height: 24px; vertical-align: middle; cursor: pointer; }

/* radio aspect */
[type="radio"]:not(:checked) + label:before, [type="radio"]:checked + label:before { content: ""; position: absolute; left: 0; top: 0; width: 20px; height: 20px; border: 2px solid #fec200; border-radius: 100%; transition: all 0.2s; }

/* checked mark aspect */
[type="radio"]:not(:checked) + label:after, [type="radio"]:checked + label:after { content: ""; position: absolute; top: 4px; left: 4px; width: 16px; height: 16px; background-color: #fec200; border-radius: 100%; transition: all 0.5s; }

/* checked mark aspect changes */
[type="radio"]:not(:checked) + label:after { opacity: 0; transform: scale(0); }

[type="radio"]:checked + label:after { opacity: 1; transform: scale(1); }

/* disabled radio */
[type="radio"]:disabled:not(:checked) + label:before, [type="radio"]:disabled:checked + label:before { box-shadow: none; border-color: #bbb; background-color: #ddd; }

[type="radio"]:disabled:checked + label:after { color: #999; }

[type="radio"]:disabled + label { color: #aaa; }

